.list{
    color: antiquewhite;
    width: 300px;
    background-color: #3f5e5e;
    height: 50px;
    border-radius: 15px;
    margin: 20px auto;
}
.list p{
    padding: 10px;
    position: relative;
}
.list p span{
    position: absolute;
    right: 10px;
    cursor: pointer;

}
.faicons{
    margin: 0 5px;
    font-size: 15px;
}
.list p input{
    background-color: transparent;
    border: 0;
    color: antiquewhite;
    font-size: 18px;

}
.list p input:focucs{
    outline: none;
}